.text-filter-form {
  color: rgba(5, 5, 5, 0.5);
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  margin-bottom: 8px;
  display: block;
}
.inp-search-form {
  /* border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  padding: 14px 122px 14px 48px; */
  border: 1px solid #05050533;
  border-radius: 15px;
  width: 335px;
  height: 46px;
  padding: 0 0 0 48px;
}

@media screen and (min-width: 768px) {
  .inp-search-form {
    width: 278px;
  }
}
.inp-time-form {
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  padding: 14px 18px 14px 18px;
  color: rgba(5, 5, 5, 0.5);
  font-family: Inter;
  font-size: 14px;
  /* font-style: normal; */
  font-weight: 500;
  width: 160px;
  height: 46px;
  line-height: 20px;
}

@media screen and (min-width: 768px) {
  .inp-time-form {
    font-size: 16px;
    width: 125px;
  }
}
.inp-area-form {
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  padding: 14px 18px;
  color: #05050580;
  width: 161px;
  font-size: 14px;
  height: 46px;
}

@media screen and (min-width: 768px) {
  .inp-area-form {
    width: 141px;
    font-size: 16px;
  }
}

.inp-ingredients-form {
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.2);
  padding: 14px 18px;
  color: #05050580;
  font-size: 14px;
  width: 188px;
  height: 46px;
  margin-bottom: 14px;
}

@media screen and (min-width: 768px) {
  .inp-ingredients-form {
    margin-bottom: 0;
  }
}
.form-icon-search {
  fill: #757575;
  transition: fill 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translate(0, -50%);
  stroke: none;
}
.form-search-input {
  position: relative;
  display: block;
}
.form-search-input input::placeholder {
  font-size: 14px;
}

@media screen and (min-width: 768px) {
  .form-search-input input::placeholder {
    font-size: 16px;
  }

  .filter-container {
    padding: 0 32px;
  }

  .filter-form {
    width: 468px;
  }
}

@media screen and (min-width: 1280px) {
  .filter-container {
    width: 100%;
    margin: 0;
  }

  .filter-form {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 767px) {
  .filter-container {
    padding-top: 40px;
  }
}

.filter-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 14px;
  margin: auto;
}

