.reset-filter {
  color: rgba(5, 5, 5, 0.5);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}
.reset-div-search {
  display: flex;
  justify-content: flex-end;
  max-width: 780px;
  height: 16px;
  margin-top: 14px;
  margin-bottom: 14px;
  /* gap: 4px; */
}
.svg-modal-reset-btn {
  border: none;
  background: none;
  color: rgba(155, 181, 55, 1);
  cursor: pointer;
  display: flex;
  gap: 4px;
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 20px;
}
.text-reset-btn {
  color: rgba(5, 5, 5, 0.5);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}
